@font-face {
  font-family: Mytupi Regular;
  font-style: normal;
  font-weight: normal;
  src: local(Mytupi Regular), url("mytupi.9f913bc8.woff") format("woff");
}

@font-face {
  font-family: Mytupi Bold;
  font-style: normal;
  font-weight: normal;
  src: local(Mytupi Bold), url("mytupiBOLD.62895751.woff") format("woff");
}

@font-face {
  font-family: Mytupi Ultra-condensed ExtraBold;
  font-style: normal;
  font-weight: normal;
  src: local(Mytupi Ultra-condensed ExtraBold), url("mytupiIMPACTO.97791ae6.woff") format("woff");
}

body {
  min-height: 100vh;
  color: #424242;
  background-color: #3a86ff;
  flex-direction: column;
  margin: 0;
  padding: 0;
  font-family: Mytupi Regular;
  display: flex;
}

.portada {
  min-height: 100vh;
  background-image: url("Estatua.5d42d939.jpg");
  background-position: center;
  background-size: cover;
  background-attachment: fixed;
}

.Sobre {
  background-image: url("ny.7f68e1d2.jpg");
}

.portCat {
  background-image: url("subway.72e982b3.svg");
}

.docs {
  background-image: url("docs.d4220db9.jpg");
}

.fondo {
  background-color: #000000b3;
  padding-bottom: 4em;
}

.pattern {
  min-height: 100vh;
  background-image: url("pattern.c24f1485.png");
  background-position: center;
  background-size: cover;
  background-attachment: fixed;
  padding-bottom: 1em;
}

header {
  background-color: #8338ec;
  border-bottom: 1em solid #ffbe0b;
}

footer {
  background-color: #8338ec;
  border-top: 1em solid #ffbe0b;
}

.navFlex {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.menuItem {
  margin: 0 1em;
}

.navLink {
  color: #ffbe0b;
  font-family: Mytupi Bold;
  font-size: 2em;
  text-decoration: none;
}

.navLink[href^="#"] {
  color: #fb5607;
}

.navLink:hover {
  color: #ff006e;
}

.grow {
  transition: all .2s ease-in-out;
}

.grow:hover {
  transform: scale(1.1);
}

nav ul {
  letter-spacing: 1px;
  flex-direction: column;
  align-items: center;
  margin-bottom: 3em;
  margin-left: -3em;
  list-style-type: none;
  display: flex;
}

#logo {
  margin: 1em;
  transition: all .2s ease-in-out;
}

#logo:hover {
  transform: scale(1.1);
}

main div, footer div {
  margin-left: auto;
  margin-right: auto;
}

footer div {
  text-align: center;
  width: 15em;
}

.footerDiv {
  color: #fff;
  margin: 2em auto auto;
  font-family: Mytupi Bold;
}

.credFondo {
  color: #fff;
  margin: 2em auto auto;
}

.footLink {
  color: #ffbe0b;
  font-family: Mytupi Bold;
  text-decoration: none;
  display: inline-block;
}

.footLink:hover {
  color: #ff006e;
}

input[type="button"] {
  color: #fff;
  cursor: pointer;
  background-color: #fb5607;
  border: 4px solid #ffbe0b;
  border-radius: 10px;
  margin: 1em 0 2em;
  padding: 16px 32px;
  font-family: Mytupi Bold;
  font-size: 1em;
  text-decoration: none;
}

#credit {
  color: #fff;
  margin-bottom: 1em;
}

h1 {
  color: #ffbe0b;
  text-align: center;
  text-shadow: 4px 4px #8338ec;
  font-family: Mytupi Bold;
  font-size: 2em;
}

h2 {
  color: #ffbe0b;
  text-shadow: 3px 3px #8338ec;
  text-align: center;
  font-family: Mytupi Bold;
  font-size: 1.5em;
}

h3 {
  color: #ffbe0b;
  text-shadow: 2px 2px #8338ec;
  font-family: Mytupi Regular;
  font-size: 1.6em;
}

#gancho {
  color: #ffbe0b;
  width: 25em;
  margin: 6em 0 4em 2em;
  font-family: Mytupi Bold;
}

.ganchoItem {
  text-align: left;
  width: 60%;
  margin: 0;
  font-size: 1.5em;
}

.ganchoItem2 {
  text-align: left;
  margin: 0;
  font-size: 3em;
}

.linkGancho {
  color: #ff006e;
  text-decoration: none;
}

.linkGancho:hover {
  color: #fff;
}

article {
  color: #424242;
  margin: 2em auto 4em;
}

.burbuja {
  width: auto;
  background-color: #fff;
  border: 10px solid #8338ec;
  border-radius: 10px;
  margin-bottom: 2em;
