  margin-right: 1em;
  padding: 16px 32px;
  font-size: 1.5em;
}

.linkArt {
  color: #ff006e;
  font-family: Mytupi Bold;
  text-decoration: none;
  display: inline-block;
}

.linkArt:hover {
  color: #fb5607;
}

legend {
  font-size: 2rem;
  font-weight: bold;
}

fieldset {
  border: 0;
}

.aviso {
  margin-bottom: 1em;
  font-size: .9rem;
}

textarea, label {
  display: inline-block;
  position: relative;
}

textarea {
  width: 100%;
  margin-top: -1em;
  margin-bottom: 0;
}

label {
  margin-bottom: 1em;
}

label input {
  width: 100%;
  height: 2em;
  margin-top: 1em;
  display: block;
}

#formSocios {
  color: #fff;
  background-color: #ff006e;
  border: #ff006e;
  border-radius: 5px;
  margin-top: -6em;
  margin-bottom: 1em;
  padding: 12px 30px;
  font-size: 1.1rem;
}

#formSocios:hover {
  border: 4px solid #8338ec;
}

.ast {
  color: #ff006e;
}

.catContainer {
  flex-direction: column;
  align-items: center;
  margin-bottom: 2em;
  display: flex;
}

.catH3 {
  text-align: center;
  color: #424242;
  text-shadow: none;
  margin: 0;
  font-size: 120%;
}

.iconosCat {
  margin-bottom: 1em;
}

.parCat {
  color: #424242;
  margin: 0;
}

.catLink {
  text-decoration: none;
  display: inline-block;
}

.docH1 {
  text-align: center;
  font-size: 2em;
}

.listas {
  width: 25em;
}

.linkList {
  color: #fff;
  font-family: Mytupi Bold;
  font-size: 2.5em;
  text-decoration: none;
  display: inline-block;
}

.linkList:hover {
  color: #ffbe0b;
}

.listas li.grow {
  width: 60%;
  margin-left: 1em;
  margin-right: 1em;
  list-style-type: none;
  position: relative;
}

.listas li.grow span {
  color: #fff;
  font-family: Mytupi Bold;
  font-size: 2.5em;
}

.listas li.grow:before {
  content: "";
  width: 32px;
  height: 32px;
  background-image: url("star.ebbc733b.png");
  background-repeat: no-repeat;
  position: absolute;
  top: 8px;
  left: -40px;
}

.artImg {
  width: 90%;
  border: .5em solid #424242;
  border-radius: 2em;
  outline: .5em solid #ffbe0b;
  margin-bottom: 3em;
}

figcaption {
  width: -moz-fit-content;
  width: fit-content;
  color: #fff;
  margin: -2em auto 2em;
}

@media (min-width: 641px) {
  nav ul {
    flex-direction: row;
    margin: 3em 4em;
  }

  footer div {
    width: 25em;
  }

  .ganchoItem {
    text-align: left;
    width: auto;
    margin: 0;
    font-size: 2em;
  }

  .ganchoItem2 {
    text-align: left;
    margin: 0;
    font-size: 8em;
  }

  #gancho {
    width: 36em;
    margin: 6em 0 10em 2em;
  }

  article {
    width: 35em;
  }

  .listas {
    width: -moz-fit-content;
    width: fit-content;
  }

  .artImg {
    width: 120%;
    border: .5em solid #424242;
    border-radius: 2em;
    outline: .5em solid #ffbe0b;
    margin-bottom: 3em;
    margin-left: -10%;
  }

  h1 {
    font-size: 5em;
  }

  h2 {
    font-size: 3em;
  }

  .catH3 {
    font-size: 1.5em;
  }

  .docH1 {
    font-size: 3em;
  }
}

@media (min-width: 1008px) {
  .navFlex {
    flex-direction: row;
    justify-content: space-between;
  }

  footer div {
    width: 60em;
  }

  #gancho {
    width: 50em;
    margin: 6em 0 16em 2em;
  }

  article {
    width: 60em;
  }

  textarea {
    width: 200%;
  }

  .catContainer {
    flex-direction: row;
  }

  .catContainer .burbuja {
    box-sizing: border-box;
    height: 250px;
    margin-bottom: 0;
    margin-left: 2em;
  }

  .catH3 {
    text-align: left;
    margin-bottom: .2em;
  }

  .artImg {
    width: 120%;
  }
}

